import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Layout from '../components/Layout'
import { Typography, ButtonBase, DialogTitle } from '@material-ui/core';
import classNames from 'classnames';
import Button from '@material-ui/core/Button';
import DeleteIcon from '@material-ui/icons/Delete';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import KeyboardVoiceICon from '@material-ui/icons/KeyboardVoice';
import Icon from '@material-ui/core/Icon';
import SaveIcon from '@material-ui/icons/Save';
import axios from 'axios'


const styles = {
    textAlign: "center",
}

const pageStyles = {
    textAlign: "center",
    height: "95vh",
    marginTop:'-10',
    backgroundImage: "url(/static/images/backgrounds/nelson.jpg)",
    backgroundSize: "cover"
}

const buttonStyle = {
    width: "0.1px",
    height: "0.1px",
    opacity: 0,
    overflow: "hidden",
    position: "absolute",
    zIndex: -1
}

const labelStyle = {
    fontSize: "1.25em",
    fontWeight: 700,
    color: "white",
    backgroundColor: "black",
    display: "inline-block",
    padding: '15px',
    marginTop: '10px',
    borderRadius: '10px',
    border: '1px solid white'

}

class upload extends Component {
    constructor() {
        super()
        this.state = {
            selectedFile: null,
            loaded: 0
        }
    }

    handleUpload = () => {
        console.log(this.state.selectedFile.name);
        const data = new FormData()
        data.append('file', this.state.selectedFile, this.state.selectedFile.name)
    }

    handleselectedFile = event => {
        // console.log(event.target.files[0])
        this.setState({
            selectedFile: event.target.files[0],
            loaded: 0,
        })
    }

    render() {
    const fileUploaded = this.state.selectedFile
    return(
        <Layout>
            <Paper elevation={"1"}>
                <Grid container spacing={24} style={pageStyles} direction="row" justifyContent="center" alignItems="center" justify="center">
                    <Grid item xs={6}>
                        <Typography variant="h5" style={styles}>
                            Step 1. Select a speech style to match
                        </Typography>
                    </Grid>

                    <Grid item xs={6}>
                        <Typography variant="body1" style={styles}>
                            Upload an audio recording of you speaking attempting to match a speech sample.
                        </Typography>
                    </Grid>
                    
                    
                    <Grid item xs={12}>
                        <Typography variant="body1" style={styles}>
                            Upload an audio recording of you speaking attempting to match a speech sample.
                        </Typography>
                        <input type="file" name="file" id="file" class="inputfile" onChange={this.handleselectedFile} style={buttonStyle}/>
                        <label for="file" style={labelStyle}> Choose An Audio File</label>
                        {fileUploaded &&
                            <Button variant="contained" color="primary" onClick={this.handleUpload}> Upload </Button>
                        }
                    </Grid>
                </Grid>
            </Paper>
        </Layout>  
    )}
}

export default upload